.messages {
	color: black;
	display: grid;
	grid-template-areas:
		'd a'
		'd m';
	grid-template-columns: 4fr 10fr;
	grid-template-rows: auto 1fr;
	background-color: lightgoldenrodyellow;
}

.messages .dialogs {
	grid-area: d;
}
.messages .dialogs .dialog {
	padding: 20px;
}
.messages .dialogs .dialog .active {
	color: coral;
}
.messageItem {
	grid-area: m;
}

.messages .message {
	padding-top: 20px;
}

.addMessage {
	grid-area: a;
	margin-top: 1.5em
}

